
// Libraries

// Styles
import styles from './style.module.scss';


const TYPES = {
  default: 'default',
  circle: 'circle'
}

const Button = ({
  type = TYPES.default,
  text = 'Кнопка',
  icon,
  buttonProps = {},
}) => {

  return (
    <button
      {...buttonProps}
      className={`
        ${styles.Button} 
        ${type === TYPES.circle ? styles.Button_circle : ''}
      `}
    >
      {type === TYPES.circle ? (
        <img src={icon} alt='icon' />
      ) : text}
    </button>
  )
}

export default Button;