
// Assets
import light from '../../../assets/landing/light.png';
import scatteringIcon from '../../../assets/landing/scattering.png';

// Styles
import styles from './style.module.scss';


const ImageWithLight = ({ children, scattering, light = true }) => {
  return (
    <div className={styles.ImageWithLight}>
      {light && (
        <div className={styles.Light} />
      )}
      {scattering && (
        <div className={styles.Scattering}>
          <img src={scatteringIcon} />
        </div>
      )}
      <div className={styles.Child}>
        {children}
      </div>
    </div>
  )
};

export default ImageWithLight;