
// Libraries
import { useState, useMemo, useEffect, createContext } from 'react';
import classnames from "classnames";

// Components
import Flags from "country-flag-icons/react/3x2";
import MainSlide from "../../components/landing/MainSlide";
import InfoBlock from "../../components/landing/InfoBlock";
import Button from "../../components/Button";
import Footer from "../../components/Footer";

// Constants
import { LANGUAGES, TEXTS } from '../../constants';

// Assets
import backgroundWithLotus from "../../assets/landing/backgroundWithLotus.png";
import slide1 from "../../assets/landing/slide1.png";
import slide2 from "../../assets/landing/slide2.png";
import slide3 from "../../assets/landing/slide3.png";
import android from "../../assets/android.svg";
import apple from "../../assets/apple.svg";
import logo from "../../assets/logo.svg";

// Styles
import styles from './style.module.scss';


export const LanguageContext = createContext(null);


const Landing = () => {
  // в будущем лучше переместить в стор, конечно же. И в index.js поместить контекст
  const [language, setLanguage] = useState(localStorage.getItem('language') || LANGUAGES.ru);

  useEffect(() => {
    localStorage.setItem('language', language);
  }, [language]);

  const textTransformer = text => text.split('\n').map(paragraph => <p>{paragraph}</p>)

  const LanguageSelector = () => (
    <div className={styles.Languages}>
      <div
        className={classnames(styles.Language, language === LANGUAGES.en ? styles.Language_selected : '')}
        onClick={() => setLanguage(LANGUAGES.en)}
      >
        <Flags.US title="United States" />
      </div>
      <div
        className={classnames(styles.Language, language === LANGUAGES.ru ? styles.Language_selected : '')}
        onClick={() => setLanguage(LANGUAGES.ru)}
      >
        <Flags.RU title="Russia" />
      </div>
    </div>
  );

  const STACKS = useMemo(() => ([
    {
      title: TEXTS.stack1_title[language],
      secondaryTitle: TEXTS.stack1_secondaryTitle[language],
      text: (
        <p>{textTransformer(TEXTS.stack1_text[language])}</p>
      ),
      background: backgroundWithLotus,
      image: slide1,
    },
    {
      secondaryTitle: TEXTS.stack2_secondaryTitle[language],
      text: (
        <>
          <p>
            {textTransformer(TEXTS.stack2_text[language])}
          </p>
          <strong style={{marginTop: 35}}>
            {TEXTS.stack2_boldText[language]}
          </strong>
        </>
      ),
      image: slide2,
      reverse: true,
    },
    {
      secondaryTitle: TEXTS.stack3_secondaryTitle[language],
      text: (
        <>
          <p>{textTransformer(TEXTS.stack3_text[language])}</p>
        </>
      ),
      image: slide3
    },
  ]), [language]);

  const Top = () => (
    <div className={styles.Top}>
      <img src={logo} />
      <LanguageSelector />
    </div>
  );

  const Stacks = () => STACKS.map((stack, counter) => <InfoBlock key={counter} {...stack} counter={counter + 1} />)

  const Enticing = () => (
    <div className={styles.Enticing}>
      {TEXTS.enticing[language]}
    </div>
  );

  const Links = () => (
    <div className={styles.Links}>
      <Button
        type='circle'
        icon={apple}
        buttonProps={{
          style: {
            marginRight: 24
          }
        }}
      />
      <Button
        type='circle'
        icon={android}
      />
    </div>
  );

  return (
    <LanguageContext.Provider value={language}>
      <div className={styles.Container}>
        <Top />
        <MainSlide />
        <Stacks />
        <Enticing />
        <Links />
        <Footer />
      </div>
    </LanguageContext.Provider>
  )
};

export default Landing;