
// Libraries
import { useContext } from "react";

// Components
import ImageWithLight from "../ImageWithLight";

// Contexts
import { LanguageContext } from "../../../pages/Landing";

// Assets
import mainPhone from '../../../assets/landing/mainPhone.png';
import apple from '../../../assets/apple-purple.svg';
import android from "../../../assets/android-purple.svg";

// Styles
import styles from './style.module.scss';
import {TEXTS} from "../../../constants";


const MainSlide = () => {
  const language = useContext(LanguageContext);

  return (
    <div className={styles.Content}>
      <div className={styles.Cols}>
        <div className={styles.TextCol}>
          <div className={styles.Title}>DitL</div>
          <div className={styles.SecondaryTitle}>
            {TEXTS.MainSlide_secondaryTitle[language]}
          </div>
          <div className={styles.Line} />
          <div className={styles.Access}>
            {TEXTS.MainSlide_access[language]}
          </div>
          <div className={styles.Links}>
            <div className={styles.Link}>
              <img src={apple} />
            </div>
            <div className={styles.Link}>
              <img src={android} />
            </div>
          </div>
        </div>
        <div className={styles.ImageCol}>
          <ImageWithLight scattering>
            <img src={mainPhone} className={styles.Image} />
          </ImageWithLight>
        </div>
      </div>
    </div>
  )
}

export default MainSlide;