
// Libraries
import {useMemo} from 'react';

// Styles
import styles from './style.module.scss';
import ImageWithLight from "../ImageWithLight";


const InfoBlock = ({
  title,
  secondaryTitle,
  text,
  background,
  image,
  counter,
  reverse
}) => {
  const reverseStyle = useMemo(() => reverse ? styles.Reverse : '', [reverse]);

  return (
    <div className={styles.InfoBlock}>
      {background && (
        <div className={styles.BackgroundImage}>
          <img src={background} />
        </div>
      )}
      <div className={styles.Content}>
        {title && (
          <div className={styles.Title}>{title}</div>
        )}
        <div className={`${styles.Cols} ${reverseStyle}`}>
          <div className={`${styles.BackgroundInfo} ${reverseStyle}`}>
            <div className={styles.Counter}>{counter}</div>
            <div className={styles.Line} />
          </div>
          <div className={styles.ImageCol}>
            <ImageWithLight light={false}>
              <img src={image} className={styles.Image} />
            </ImageWithLight>
          </div>
          <div className={styles.TextCol}>
            {secondaryTitle && (
              <div className={styles.SecondaryTitle}>{secondaryTitle}</div>
            )}
            {text && (
              <div className={styles.Text}>{text}</div>
            )}
          </div>
        </div>
      </div>
    </div>
  )
};

export default InfoBlock;